body, p, h1, h2, h3, h4, h5, h6, ul, ol, li, dl, dt, dd, table, th, td, form, fieldset, legend, input, textarea, button, select {
  margin: 0;
  padding: 0;
  -webkit-text-size-adjust: none;
}

.blind {
  overflow: hidden;
  position: absolute;
  top: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  font-size: 0;
  line-height: 0;
  visibility: visible;
}

a {
  text-decoration: none;
}

ul, ol {
  list-style: none;
}
